.footer-copyright {
  font-size: 1.4rem;
  font-weight: 400;
  letter-spacing: 0;
  color: #666;
}

.social-icons a:hover, .social-icons a:focus {
  color: #fff;
}

.footer-bottom .container,
.footer-bottom .container-fluid {
  padding-bottom: 2.1rem;
}

.footer .widget-title {
  text-transform: uppercase;
  color: #fff;
}

.widget-about p {
  max-width: 260px;
}

.owl-simple .owl-nav .owl-prev:focus,
.owl-simple .owl-nav .owl-next:focus {
  outline: none;
}

@media screen and (min-width: 1200px) {
  .banners.center .row {
    margin-left: -.5rem;
    margin-right: -.5rem;
  }
  .banners.center .banner {
    flex: 0 0 31.3%;
    max-width: 31.3%;
  }
  .banners.center .banner.percent {
    flex: 0 0 37.4%;
    max-width: 37.4%;
  }
}

@media screen and (max-width: 1199px) {
  .banners.center .banner {
    align-items: inherit;
  }
  .deal {
    padding: 0;
  }
  .deal-countdown {
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 991px) {
  .banner-lg, .banner-sm {
    display: flex;
    justify-content: center;
    overflow-x: hidden;
  }
  .banner-sm.font-white .intro,
  .banner-sm.font-black .intro {
    left: 50%;
    top: 50%;
    right: initial;
    transform: translate(-50%, -50%);
  }
  .footer-middle .row > div:not(:first-child) {
    padding-left: 3rem;
  }
  .footer-middle .row > div {
    padding-left: 3rem;
  }
  .widget-about p {
    max-width: 80%;
  }
}

@media screen and (max-width: 767px) {
  .deal {
    margin-bottom: 2rem;
  }
  .product.product-10.product-lg .product-action {
    margin-top: 2rem;
    position: relative;
    transform: translateY(0);
  }
  .product.product-10.product-lg:hover .product-action, .product.product-10.product-lg:focus .product-action {
    transform: translateY(0);
  }
}

@media screen and (max-width: 877px) {
  .slider .intro {
    padding-top: 6rem;
  }
  .slider img {
    height: calc(100% + 53vh);
  }
}

@media screen and (max-width: 369px) {
  .product.product-10:not(.product-lg) .btn-cart {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
